.toggle {
  align-items: center;
  display: flex;
}

.toggle__input {
  width: 1px;
  height: 1px;
  position: absolute;
  overflow: hidden;
}

.toggle-track {
  width: 60px;
  height: 30px;
  cursor: pointer;
  background: none;
  border: 2px solid #bdc3c7;
  border-radius: 15px;
  margin-right: 12px;
  transition: background-color .3s ease-in-out;
  display: flex;
  position: relative;
}

.toggle-indicator {
  width: 23px;
  height: 23px;
  background: #b10000;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  transition: all .3s;
  display: flex;
  position: absolute;
  bottom: 2px;
  left: 2px;
}

.toggle__input:checked + .toggle-track .toggle-indicator {
  background: #bdc3c7;
  transform: translateX(30px);
}

.toggle__input:checked + .toggle-track {
  background: #00ba10;
}

.backdrop-loader {
  z-index: 100;
  width: 100%;
  height: 100%;
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  background: rgba(46, 47, 66, .4);
  justify-content: center;
  align-items: center;
  transition: all .5s cubic-bezier(.075, .82, .165, 1);
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.backdrop-loader-show {
  opacity: 1;
  visibility: visible;
  pointer-events: auto;
}

